:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1216px;
  --container-offset: 23px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --black-color: #000;
  --text-color: #060606;
  --primary-green: #1baa7d;
  --neutral-dark: #494949;
  --neutral-lightest: #f5f5f5;
  --neutral-darkest: #303030;
}

/* stylelint-disable */
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  border-radius: 20px;
  padding: 12px 20px;
}
.btn--primary {
  color: var(--light-color);
  background: var(--primary-green);
}
.btn--primary:hover {
  color: var(--primary-green);
  background: var(--light-color);
  border: 1px solid var(--primary-green);
}
.btn--secondary {
  color: var(--black-color);
  background: var(--light-color);
}
.btn--secondary:hover {
  background-color: var(--primary-green);
  color: var(--light-color);
  border: 1px solid var(--light-color);
}

.section__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 44px;
  text-align: center;
  color: var(--neutral-darkest);
}
@media (max-width: 611px) {
  .section__title {
    font-size: 37px;
  }
}
@media (max-width: 576px) {
  .section__title {
    font-size: 31px;
  }
}

.swiper {
  width: 100%;
  height: 100%;
}

